"use client";

import { useUser, RedirectToSignIn } from "@clerk/nextjs";
import Image from "next/image";
import { Clock, Heart, Settings, LogOut, Edit2, Save } from "lucide-react";
import { SignOutButton } from "@clerk/nextjs";
import { useContext, useEffect, useState } from "react";
import { WatchlistContext } from "@/context/watchlist-context";
import { MovieCard } from "@/components/MovieCard";

export default function ProfilePage() {
    const { isLoaded, isSignedIn, user } = useUser();
    const { items } = useContext(WatchlistContext) as any;
    const [bio, setBio] = useState("");
    const [isEditingBio, setIsEditingBio] = useState(false);

    useEffect(() => {
        if (typeof window !== 'undefined') {
            const savedBio = localStorage.getItem("user_bio");
            if (savedBio) setBio(savedBio);
        }
    }, []);

    const saveBio = () => {
        setIsEditingBio(false);
        localStorage.setItem("user_bio", bio);
    };

    if (!isLoaded) return <div className="min-h-screen bg-bg-main flex items-center justify-center text-white">Loading...</div>;

    if (!isSignedIn) {
        return <RedirectToSignIn />;
    }

    return (
        <main className="min-h-screen pt-24 pb-20 bg-bg-main">
            <div className="container mx-auto px-6 lg:px-20">
                {/* Header */}
                <div className="flex flex-col md:flex-row items-center gap-8 mb-12 bg-bg-card p-8 rounded-2xl border border-white/5 relative overflow-hidden">
                    {/* Background Glow */}
                    <div className="absolute top-0 right-0 w-96 h-96 bg-accent-primary/10 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2" />

                    <div className="relative">
                        <div className="w-32 h-32 rounded-full border-4 border-bg-main overflow-hidden ring-4 ring-white/10 relative group">
                            <Image
                                src={user.imageUrl}
                                alt={user.fullName || "User"}
                                fill
                                className="object-cover"
                            />
                        </div>
                    </div>

                    <div className="flex-1 text-center md:text-left relative z-10 w-full">
                        <h1 className="text-3xl font-display font-bold text-white mb-2">{user.fullName}</h1>
                        <p className="text-text-secondary mb-4">{user.primaryEmailAddress?.emailAddress}</p>

                        {/* Bio Section */}
                        <div className="mb-4 max-w-xl">
                            {isEditingBio ? (
                                <div className="flex gap-2">
                                    <input
                                        autoFocus
                                        type="text"
                                        value={bio}
                                        onChange={(e) => setBio(e.target.value)}
                                        placeholder="Add a bio..."
                                        className="bg-black/50 border border-white/20 rounded px-3 py-1 text-sm text-white w-full focus:outline-none focus:border-accent-primary"
                                        onKeyDown={(e) => e.key === 'Enter' && saveBio()}
                                    />
                                    <button onClick={saveBio} className="p-1 hover:text-accent-primary text-white"><Save size={16} /></button>
                                </div>
                            ) : (
                                <p className="text-text-muted text-sm flex items-center justify-center md:justify-start gap-2 group cursor-pointer" onClick={() => setIsEditingBio(true)}>
                                    {bio || "Add a bio..."}
                                    <Edit2 size={12} className="opacity-0 group-hover:opacity-100 transition-opacity" />
                                </p>
                            )}
                        </div>

                        <div className="flex flex-wrap gap-3 justify-center md:justify-start">
                            <div className="px-4 py-2 rounded-full bg-white/5 border border-white/10 text-sm text-text-muted flex items-center gap-2">
                                <Heart size={14} className="text-accent-primary" /> {items.length} Saved
                            </div>
                        </div>
                    </div>

                    <div className="flex gap-4 relative z-10">
                        <button className="p-3 rounded-full bg-white/5 hover:bg-white/10 border border-white/10 text-white transition-colors">
                            <Settings size={20} />
                        </button>
                        <SignOutButton>
                            <button className="p-3 rounded-full bg-red-500/10 hover:bg-red-500/20 border border-red-500/20 text-red-500 transition-colors">
                                <LogOut size={20} />
                            </button>
                        </SignOutButton>
                    </div>
                </div>

                {/* Content Tabs */}
                {/* Watchlist */}
                <div className="space-y-6">
                    <h2 className="text-xl font-bold text-white flex items-center gap-2">
                        <span className="w-1 h-6 bg-accent-primary rounded-full" />
                        My Watchlist
                    </h2>
                    {items.length > 0 ? (
                        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-6">
                            {items.map((movie: any) => (
                                <MovieCard key={movie.id} movie={movie} />
                            ))}
                        </div>
                    ) : (
                        <div className="py-20 text-center bg-bg-card rounded-2xl border border-white/5 border-dashed">
                            <p className="text-text-secondary">Your watchlist is empty.</p>
                            <p className="text-text-muted text-sm mt-2">Browse movies and click the + button to add them.</p>
                        </div>
                    )}
                </div>
            </div>
        </main>
    );
}
